<template>
  <transition name="slide">
    <div>
        <div class="head">
            <img src="static/doctor.jpg" class="doctorImg">
            <div class="doctorInfo">
                <div class="honourBack">
                    <span class="doctorHonour">朱氏头皮针</span>
                </div> 
                <div>
                    <span>朱明清</span>
                    <span class="doctorPosition">教授</span>
                </div>
                <div>
                    <span class="verbCost">￥200</span>
                </div>
            </div>
        </div>
        <div class="introduce">
            <div class="introduceLeft">
                <img src="static/verb.png" class="verbImg">
            </div>
            <div class="introduceRight">
                <pre v-bind:class="{ preHeight: foldOpen }">美籍华人，美国朱氏头皮针医学教育基金会董事长。
    【朱氏头皮针】创始人：
    “朱氏针灸神经医学中心”首席顾问及主任医师；
    “朱氏头皮针研究暨教育基金会”主席；
    南京中医药大学兼职教授，研究生导师；
    美国俄勒冈州波特兰中医大学博士生导师；
    香港港九中医师公会永远荣誉会长。</pre>
            </div>
        </div>
        <div class="fold" @click="foldClick">
            <span v-show="foldOpen">...</span>
            <img :src="foldImgUrl">
        </div>
        <div class="verbDateBoxOut">
            <div class="verbDateBox" ref="verbDateBox">
                <div class="verbDate" v-for="(item, index) in dateList" :key="index" ref="verbDate">
                    <span>{{item.date}}</span>
                    <span>{{item.day}}</span>
                    <div v-bind:class="{ noVerbDate: !item.status, hasVerbDate:  item.status }">
                        <span v-if="item.status">预约</span>
                        <span v-if="!item.status">约满</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'doctorVerb',
  created: function () {
    console.log('页面开始加载')
    var date = new Date()
    for (var i = 0; i < 7; i++) {
      var addDate = new Date((date / 1000 + 86400 * i) * 1000)
      var str = addDate.getMonth() + '-' + addDate.getDate()
      var day
      var status
      if (addDate.getDay() === 1) {
        day = '周一'
        status = 1
      } else if (addDate.getDay() === 2) {
        day = '周二'
        status = 0
      } else if (addDate.getDay() === 3) {
        day = '周三'
        status = 1
      } else if (addDate.getDay() === 4) {
        day = '周四'
        status = 1
      } else if (addDate.getDay() === 5) {
        day = '周五'
        status = 1
      } else if (addDate.getDay() === 6) {
        day = '周六'
        status = 1
      } else if (addDate.getDay() === 0) {
        day = '周日'
        status = 0
      }
      this.dateList.push({date: str, day: day, status: status})
    }
  },
  mounted () {
    var verbDataWidth = `${document.documentElement.clientWidth}` / 5
    this.$refs.verbDateBox.style.width = verbDataWidth * 7 + 'px'
  },
  data () {
    return {
      foldImgUrl: 'static/down.png',
      foldOpen: true,
      dateList: []
    }
  },
  methods: {
    foldClick: function () {
      this.foldOpen = !this.foldOpen
      if (this.foldOpen) {
        this.foldImgUrl = 'static/down.png'
      } else {
        this.foldImgUrl = 'static/up.png'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

$img_height_doctor = 80px
.doctorImg
    width $img_height_doctor
    height $img_height_doctor
    border-radius $img_height_doctor/2
    border 2px solid $color-theme-blue
    margin-left 20px

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height $img_height_doctor + 40
    background-color white

.doctorInfo
    @extend .flex
    @extend .flex-column
    justify-content space-around
    text-align left
    margin-left 20px
    height $img_height_doctor
    span
        font-weight $font-weight-level_6

.doctorPosition
    margin-left 10px

.doctorHonour
    font-size $font-size-level_0
    color white

.honourBack
    background-color $color-theme-blue
    width 100px
    height 20px
    border-radius 3px
    @extend .flex-around

.verbCost
    color $color-theme-gray_1
    font-size $font-size-level_2

.introduce
    @extend .flex
    @extend .flex-row
    justify-content: flex-start
    align-items: flex-start
    background-color white

.verbImg
    width 24px
    margin-top 6px
    margin-left 20px

.introduceLeft
    width 40px

.introduceRight
    flex-grow 2
    pre
        white-space: pre-wrap
        word-wrap: break-word
        margin 10px 25px 0px 25px
        color $color-theme-gray_2
        line-height 17px
        font-size 10px
        overflow hidden

.preHeight
    height 50px

.fold
    @extend .flex
    @extend .flex-column
    @extend .flex-around
    background-color white
    img
        width 22px

.verbDateBoxOut
    overflow-x scroll

.verbDateBox
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    background-color rgb(242, 242, 242)
    height 100px

.verbDate
    @extend .flex
    @extend .flex-column
    @extend .flex-around
    width 50%
    span
        color black
        font-size 13px
        line-height $font-size-level_6
        font-weight $font-weight-level_3
    div
        border-radius 3px
        height $font-size-level_6
        width 70%
        margin-top 3px
        span
            
            line-height $font-size-level_6
            font-size $font-size-level_2

.noVerbDate
    border 1px solid $color-theme-gray_1
    span
        color $color-theme-gray_1

.hasVerbDate
    border 1px solid $color-theme-blue
    span
        color $color-theme-blue
        
</style>



